@font-face {
  font-family: MontSeratLight;
  src: url(../fonts/montserrat/Montserrat-Light.ttf);
}

@font-face {
  font-family: MontSeratRegular;
  src: url(../fonts/montserrat/Montserrat-Regular.ttf);
}

/* start theme */
html,
body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

body {
  font-family: MontSeratLight, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 14px;
  color: #212121;
}

/* header */
header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 70px;
  line-height: 70px;
  background-color: #2764b6;
  background-image: -webkit-gradient(linear, left top, right top, from(#2764b6), to(#303bb5));
  background-image: -webkit-linear-gradient(left, #2764b6, #303bb5);
  background-image: -moz-linear-gradient(left, #2764b6, #303bb5);
  background-image: -ms-linear-gradient(left, #2764b6 #303bb5);
  background-image: -o-linear-gradient(left, #2764b6, #303bb5);
  background-image: linear-gradient(left, #2764b6, #303bb5);
  color: #ffffff;
  z-index: 1030;
  -webkit-box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.03), 0 2px 5px 0 rgba(0, 0, 0, 0.05);
  -moz-box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.03), 0 2px 5px 0 rgba(0, 0, 0, 0.05);
  box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.03), 0 2px 5px 0 rgba(0, 0, 0, 0.05);
}

header nav {
  position: relative;
}

header nav .logo-section a {
  color: inherit;
  text-decoration: none;
  font-size: 16px;
  line-height: 70px;
  font-weight: bold;
}

header nav .main-menu-section {
  position: static;
}

header nav .main-menu-section ul.mainMenu {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: static;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -ms-flex-flow: row nowrap;
  flex-flow: row nowrap;
  justify-content: space-around;
  max-height: 70px;
}

header nav .main-menu-section ul.mainMenu li {
  height: 70px;
}

header nav .main-menu-section ul.mainMenu li a {
  color: inherit;
  font-weight: bold;
  line-height: 70px;
  display: inline-block;
}

header nav .main-menu-section ul.mainMenu li a .material-icons {
  line-height: 70px;
  float: right;
}

header nav .main-menu-section ul.mainMenu li a:hover {
  line-height: 66px;
  border-bottom: 4px solid #cbdeee;
  text-decoration: none;
}

header nav .main-menu-section ul.mainMenu li a:hover .material-icons {
  line-height: 66px;
}

header nav .main-menu-section ul.mainMenu li .sub-menu-columns {
  display: none;
  position: absolute;
  width: auto;
  top: 70px;
  left: 0;
  min-height: 300px;
  box-sizing: border-box;
  padding: 20px 0;
  border-right: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
  border-left: 1px solid #ddd;
  border-radius: 0 0 4px 4px;
  background-color: #f8fafb;
  -webkit-box-shadow: 0 2px 42px 0 rgba(0, 0, 0, 0.16);
  box-shadow: 0 2px 42px 0 rgba(0, 0, 0, 0.16);
  list-style: none;
  text-align: left;
  list-style-type: none;
}

header nav .main-menu-section ul.mainMenu li .sub-menu-columns li {
  color: #212121;
  float: left;
  width: 230px;
  height: auto;
  line-height: 1.5;
  padding: 15px;
  border-right: 1px solid #e3e3e3;
}

header nav .main-menu-section ul.mainMenu li .sub-menu-columns li ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  border: none;
  position: static;
}

header nav .main-menu-section ul.mainMenu li .sub-menu-columns li ul li {
  border: none;
  display: block;
  width: 100%;
  height: 50px;
  font-size: 14px;
}

header nav .main-menu-section ul.mainMenu li .sub-menu-columns li ul li a {
  display: block;
  line-height: 1.5;
}

header nav .main-menu-section ul.mainMenu li .sub-menu-columns li ul li a i {
  color: #2764b6;
  font-size: 20px;
  line-height: 1.5;
  margin-right: 5px;
}

header nav .main-menu-section ul.mainMenu > li:hover .sub-menu-columns {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

header nav .right-section {
  text-align: right;
  height: 70;
}

header nav .right-section > a {
  color: inherit;
  font-weight: bold;
  line-height: 70px;
  display: inline-block;
}

header nav .right-section > a > .material-icons,
header nav .right-section > a i {
  line-height: 70px;
  float: left;
  margin-right: 5px;
  font-size: 20px;
}

header nav .right-section > a:hover {
  line-height: 66px;
  border-bottom: 4px solid #cbdeee;
  text-decoration: none;
}

header nav .right-section > a:hover > .material-icons,
header nav .right-section > a:hover i {
  line-height: 66px;
}

.mobile-side-nav {
  height: 100%;
  width: 285px;
  position: fixed;
  z-index: 1030;
  top: 70px;
  right: -285px;
  background-color: #f8fafb;
  box-shadow: 0 2px 42px 0 rgba(0, 0, 0, 0.16);
  border-left: 1px solid #ddd;
  overflow-x: auto;
  transition: 0.5s;
}

.mobile-side-nav ul {
  list-style-type: none;
  margin: 0px;
  padding: 0px;
}

.mobile-side-nav ul li {
  display: block;
  width: 100%;
  text-align: left;
  border-bottom: 1px solid #dddddd;
  padding: 20px 15px;
}

.mobile-side-nav ul li a {
  display: block;
  text-decoration: none;
  font-size: 14px;
  color: #212121;
  font-weight: bold;
}

.mobile-side-nav ul li a i {
  float: right;
  color: #2764b6;
  font-size: 18px;
}

.mobile-side-nav ul li .drop-down-menu {
  display: none;
  margin-top: 15px;
  background-color: #ffffff;
  border: 1px solid #ddd;
}

.mobile-side-nav ul li .drop-down-menu li.hasSubMenu {
  padding: 0px;
}

.mobile-side-nav ul li.show-drop-down .drop-down-menu {
  display: block;
}

.mobile-side-nav.show {
  right: 0;
}

.main-container {
  width: 100%;
  height: 100%;
  padding-top: 100px;
}

.top-title {
  text-align: center;
}

.top-title > h1 {
  font-size: 26px;
  font-weight: bold;
  color: #212121;
}

.top-title > p {
  font-size: 18px;
  color: #616161;
}

.cards-links-group {
  margin-top: 15px;
}

.cards-links-group a {
  -webkit-box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.16), 0 2px 5px 0 rgba(0, 0, 0, 0.26);
  -moz-box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.16), 0 2px 5px 0 rgba(0, 0, 0, 0.26);
  box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.16), 0 2px 5px 0 rgba(0, 0, 0, 0.26);
  -moz-border-radius: 2px;
  -webkit-border-radius: 2px;
  border-radius: 2px;
  background: #ffffff;
  padding: 0px 15px;
  line-height: 65px;
  margin-bottom: 25px;
  display: block;
  border-left: 6px solid #00bcd4;
  text-decoration: none;
  color: #212121;
  font-size: 15px;
}

.cards-links-group a > .material-icons {
  float: right;
  color: #00bcd4;
  font-size: 20px;
  font-weight: bold;
  line-height: 65px;
  transition: transform 0.36s;
}

.cards-links-group a:hover {
  box-shadow: 0 5px 13px 0 #999999;
}

.cards-links-group a:hover .material-icons {
  transform: translateX(30%);
}

.newspaper-section {
  background: #f7faff;
  border: 1px solid #dbe7ff;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin-top: 20px;
}

.newspaper-section img {
  margin: 10px;
}

.newspaper-section span {
  margin: 10px;
  font-size: 15px;
  font-weight: bold;
}

.custom-card {
  -webkit-box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.16), 0 2px 5px 0 rgba(0, 0, 0, 0.26);
  -moz-box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.16), 0 2px 5px 0 rgba(0, 0, 0, 0.26);
  box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.16), 0 2px 5px 0 rgba(0, 0, 0, 0.26);
  -moz-border-radius: 2px;
  -webkit-border-radius: 2px;
  border-radius: 2px;
  background: #ffffff;
  margin-bottom: 30px;
  box-sizing: border-box;
}

.recommended-cards-group {
  margin-top: 45px;
  margin-bottom: 15px;
}

.recommended-cards-group .recommended-cards-title {
  margin-bottom: 45px;
}

.recommended-cards-group .recommended-cards-title h1 {
  font-size: 26px;
  font-weight: bold;
  color: #212121;
}

.recommended-cards-group .recommended-card {
  padding: 20px 10px 20px 20px;
}

.recommended-cards-group .recommended-card .custom-card-image img {
  width: 100%;
  min-height: 160px;
}

.recommended-cards-group .recommended-card .custom-card-info {
  margin-left: -10px;
}

.recommended-cards-group .recommended-card .custom-card-info h1 {
  font-size: 20px;
  font-weight: bold;
  line-height: 25px;
}

.recommended-cards-group .recommended-card .custom-card-info p.card-title-hint {
  font-size: 14px;
  font-weight: bold;
}

.recommended-cards-group .recommended-card .custom-card-info p.card-title-hint .custom-star {
  font-weight: bold;
  font-size: 14px;
  color: #92c7f3;
}

.recommended-cards-group .recommended-card .custom-card-info p.card-title-hint .active-star {
  color: #2764b6;
}

.recommended-cards-group .recommended-card .custom-card-info p.card-info-body {
  margin-bottom: 7px;
}

.recommended-cards-group .recommended-card .custom-card-buttons .btn {
  display: block;
  margin-bottom: 10px;
  padding: 0px;
}

.recommended-cards-group .more-cards-link {
  border: 1px solid #2764b6;
  border-radius: 5px;
  color: #2764b6;
  font-size: 15px;
  font-weight: 700;
  padding: 0 30px;
  height: 50px;
  display: inline-block;
  line-height: 50px;
}

.recommended-cards-group .more-cards-link .material-icons {
  float: right;
  line-height: 50px;
}

.we-are-section {
  margin-top: 50px;
  margin-bottom: 70px;
}

.we-are-section img {
  max-width: 100%;
  min-width: 100%;
  border-radius: 10px;
}

.we-are-section h1 {
  font-size: 20px;
  font-weight: bold;
}

.we-are-section p {
  font-size: 14px;
}

.footer-email-section {
  padding: 25px 0px 35px 0px;
  background-color: #f2f2f2;
}

.footer-email-section h1 {
  font-size: 18px;
  font-weight: bold;
  line-height: 25px;
  margin-bottom: 15px;
}

.footer-email-section .email {
  width: 350px;
  padding: 7px;
  display: inline-block;
  max-width: 100%;
  margin-bottom: 15px;
}

.footer-email-section .email:focus {
  outline: 1px solid #2764b6;
}

.footer-email-section .subscribe {
  padding: 0 25px;
  background-color: #2764b6;
  color: #ffffff;
  font-weight: bold;
  border-radius: 5px;
  outline: none;
  margin-left: 7px;
  border: 1px solid #2764b6;
  height: 40px;
}

footer {
  padding: 30px 0px;
  background: #f8fafb;
  min-height: 300px;
}

footer .logo-section a.logo {
  font-size: 20px;
  font-weight: bolder;
  color: #2764b6;
  line-height: 35px;
  text-decoration: none;
  display: block;
}

footer .logo-section .follow-us-section {
  margin-top: 35px;
}

footer .logo-section .follow-us-section h4 {
  font-size: 16px;
  font-weight: bold;
  line-height: 35px;
}

footer .logo-section .follow-us-section a {
  text-decoration: none;
  width: 44px;
  height: 44px;
  padding: 0;
  border-radius: 50%;
  background-color: rgba(0, 0, 0, 0.3);
  color: #ffffff;
  text-align: center;
  display: inline-block;
  margin-right: 7px;
}

footer .logo-section .follow-us-section a i {
  line-height: 44px;
  font-size: 20px;
}

footer h3 {
  font-size: 16px;
  font-weight: bold;
  line-height: 35px;
}

footer ul {
  list-style-type: none;
  margin: 0px;
  padding: 0px;
}

footer ul li a {
  font-size: 14px;
  line-height: 25px;
  color: #212121;
}

.text-align-center {
  text-align: center;
}

body .btn-primary {
  padding: 0 25px;
  color: #ffffff;
  background-color: #2764b6;
  background: #2764b6;
  border: 1px solid #2764b6;
  line-height: 40px;
  font-weight: bold;
  text-decoration: none;
  border-radius: 5px;
}

body .btn-secondary {
  padding: 0 25px;
  color: #616161;
  background-color: #ffffff;
  background: #ffffff;
  border: 1px solid #616161;
  font-weight: normal;
  line-height: 40px;
  text-decoration: none;
  border-radius: 5px;
}

body .btn-secondary:hover,
body .btn-secondary:focus {
  background-color: #ffffff;
  color: #212121;
}

@media only screen and (max-width: 768px) {
  [class*="col-12"] {
    margin-bottom: 15px;
  }
  .cards-links-group a {
    margin-bottom: 10px;
  }
  .newspaper-section span {
    width: 100%;
    text-align: center;
  }
  .newspaper-section img {
    max-width: 40%;
    max-height: 22px;
    margin: 20px;
  }
  .recommended-cards-group .recommended-card .custom-card-image img {
    width: 90%;
    margin-left: 5%;
  }
  .recommended-cards-group .recommended-card .custom-card-info {
    margin-left: 0px;
    text-align: center;
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}
